:root {
  --animR: fade_right 0.3s ease-out 1s normal forwards;
  --animL: fade_left 0.3s ease-out 1s normal forwards;
  --color1: #ffeeee
   
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  position: relative;

  ul {
    list-style: none;
    margin: 0;
  }

  a {
    text-decoration: none;
    color: inherit;
  }

  box-sizing: border-box;
  display: block;
  font-family: 'Roboto',  sans-serif;
}